<template>
    <div class="showtable">
        <ul class="nav nav-tabs">
            <li class="nav-item">
                <a class="nav-link" :class="{active: selected_id === 0}" @click="select(0)" aria-current="page" href="#">历史记录</a>
            </li>
            <li class="nav-item" v-for="query in query_arr" :key="query">
                <a class="nav-link" :class="{active: selected_id === query}" @click="select(query)" href="#">查询{{ query }}</a>
            </li>
        </ul>
        <table class="table table-striped" v-if="selected_id === 0">
            <thead>
                <tr>
                    <th scope="col">查询id</th>
                    <th scope="col" v-for="item in query_content_title" :key="item">{{ item }}</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in query_content" :key="item.id" @click="getdetail(item.id)" class="history">
                    <th scope="row">{{ item.id }}</th>
                    <td v-for="col in query_content_title" :key="col">{{ item[col] }}</td>
                </tr>
            </tbody>
        </table>
        <table class="table table-striped" v-else>
            <thead>
                <tr>
                    <th scope="col">id</th>
                    <th scope="col" v-for="item in query_content_title" :key="item">{{ item }}</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in query_content" :key="item.id">
                    <th scope="row">{{ item.id }}</th>
                    <td v-for="col in query_content_title" :key="col">{{ item[col] }}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
import { useStore } from 'vuex';

export default {
    name: "ShowTable",
    props: {
        query_arr: {
            type: Object,
            required: true,
        },
        query_content: {
            type: Object,
            required: true,
        },
        query_content_title: {
            type: Object,
            required: true,
        },
        selected_id: {
            type: Number,
            required: true,
        },
    },
    setup(props,context) {
        const store = useStore();
        const select = query_id => {
            store.dispatch('checkout',{
                success(){
                    context.emit('select',query_id);
                    
                },
                error(){
                    
                }
            });
            
        }
        const getdetail = query_id =>{
            store.dispatch('checkout',{
                success(){
                    context.emit('getdetail',query_id);
                },
                error(){
                    
                }
            });
        }
        return {
            select,
            getdetail,
        }
    }
}
</script>

<style>
.showtable{
    margin-top: 1vh
}
.table{
    border-left: 1px solid rgb(222,226,230);
    border-right: 1px solid rgb(222,226,230);
}
.history{
    cursor: pointer;
}
</style>