<template>
    <div class="container">
        <div class="row justify-content-md-center">
            <div class="col-5">
                <form @submit.prevent="register">
                    <div class="mb-3">
                        <label for="exampleInputEmail1" class="form-label">用户名</label>
                        <input v-model="username" type="text" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp">
                    </div>
                    <div class="mb-3">
                        <label for="exampleInputPassword1" class="form-label">密码</label>
                        <input v-model="password" type="password" class="form-control" id="exampleInputPassword1">
                    </div>
                    <div class="mb-3">
                        <label for="exampleInputPassword2" class="form-label">确认密码</label>
                        <input v-model="password_confirm" type="password" class="form-control" id="exampleInputPassword2">
                    </div>
                    <div class="error-message">{{ error_message }}</div>
                    <button type="submit" class="btn btn-primary">提交</button>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
import {ref} from 'vue';
import router from '@/router/index';
import { useStore } from 'vuex';

export default {
    name: "RegisterView",
    setup() {
        const store = useStore();
        store.dispatch('checkout',{
            success(){
                router.push({name: 'home'});
            },
            error(){
                
            }
        });
        let username = ref('');
        let password = ref('');
        let password_confirm = ref('');
        let error_message = ref('');
        const register = () => {
            error_message.value = '';
            store.dispatch('register',{
                username: username.value,
                password: password.value,
                password_confirm: password_confirm.value,
                success() {
                    router.push({name: 'home'});
                },
                error(res) {
                    error_message.value = res;
                }
            })
        }
        return {
            register,
            username,
            password,
            password_confirm,
            error_message,
        }
    }
}

</script>

<style scoped>
.row{
    margin-top: 10vh;
}
button{
    width: 100%;
}
.error-message {
  color: red;
}

</style>