<script>
export default {
    name: "ToolBar",
    setup(props, context) {
        const submitcode = () => {
            context.emit('submitcode');
        }
        const stopcode = () => {
            context.emit('stopcode');
        }
        const copycode = () => {
            context.emit('copycode');
        }
        const pastecode = () => {
            context.emit('pastecode');
        }
        const deletecode = () => {
            context.emit('deletecode');
        }

        return {
            submitcode,
            copycode,
            pastecode,
            deletecode,
            stopcode,
        }
    }
}
</script>

