import { createStore } from 'vuex'
import $ from 'jquery'
import jwt_decode from 'jwt-decode'

export default createStore({
  state: {
    islogin: false,
    username: '',
  },
  getters: {
  },
  mutations: {
    changelogin(state, data){
      state.islogin = true;
      state.username = data.username;
    },
    changelogout(state){
      state.islogin = false;
      state.username = '';
    }
  },
  actions: {
    register(context, data){
      $.ajax({
        url: 'https://app3148.acapp.acwing.com.cn/userregister/',
        type: 'POST',
        data: {
          username: data.username,
          password: data.password,
          password_confirm: data.password_confirm,
        },
        success(res) {
          if (res.result === "success") {
            context.dispatch('login',{
              username: data.username,
              password: data.password,
              success() {
                data.success()
              }
            })
          }
          else {
            data.error(res.result);
          }
        },
        error() {
        }
      })
    },
    login(context, data){
      $.ajax({
        url: "https://app3148.acapp.acwing.com.cn/api/token/",
        type: "POST",
        data: {
          username: data.username,
          password: data.password,
        },
        success(res) {
          const {access, refresh} = res;
          const access_obj = jwt_decode(access);

          let localstorage = window.localStorage;
          localstorage.setItem('islogin','true');
          localstorage.setItem('access',access);
          localstorage.setItem('refresh',refresh);
          localstorage.setItem('user_id',access_obj.user_id);
          localstorage.setItem('username',data.username);
          context.commit('changelogin',{username: data.username});
          data.success();
        },
        error(){
          data.error();
        }
      })

    },
    logout(context){
      let localstorage = window.localStorage;
      localstorage.clear();
      localstorage.setItem('islogin','false');
      context.commit('changelogout');
    },
    checkout(context,data){
      let localstorage = window.localStorage;
      if (localstorage.getItem("islogin") === "true") {
        const refresh = localstorage.getItem('refresh');
        context.dispatch('verify',{
          token: refresh,
          success(){
            context.dispatch('refresh');
            context.dispatch('getinfo');
            data.success();
          },
          error(){
            localstorage.clear();
            localstorage.setItem('islogin','false');
            context.commit('changelogout');
            data.error();
          }
        })
      }
      else {
        localstorage.clear();
        localstorage.setItem('islogin','false');
        context.commit('changelogout');
      }
    },
    refresh(){
      let localstorage = window.localStorage;
      const refresh = localstorage.getItem('refresh');
      $.ajax({
        url: "https://app3148.acapp.acwing.com.cn/api/token/refresh/",
        type: "POST",
        data: {
          refresh,
        },
        success(res){
          localstorage.setItem('access',res.access);
        },
      })
    },
    verify(context,data){
      const token = data.token;
      $.ajax({
        url: "https://app3148.acapp.acwing.com.cn/api/token/verify/",
        type: "POST",
        data: {
          token,
        },
        success(){
          data.success();
        },
        error(){
          data.error();
        }
      })
    },
    getinfo(context){
      let localstorage = window.localStorage;
      const access = localstorage.getItem('access');
      $.ajax({
        url: "https://app3148.acapp.acwing.com.cn/getinfo/",
        type: "GET",
        headers: {
          'Authorization': "Bearer " + access,
        },
        success(res){
          context.commit('changelogin',{username: res.username});
        }
      })
    }
  },
  modules: {
  }
})
