<template>
    <div class="container">
        <div class="card">
            <div class="card-body">
                <ToolBar @submitcode="submitcode" @stopcode="stopcode" @copycode="copycode" @pastecode="pastecode" @deletecode="deletecode" />
                <codemirror
                    v-model="code"
                    placeholder=""
                    class="code"
                    :style="{ height: '60vh'}"
                    :autofocus="true"
                    :indent-with-tab="true"
                    :tabSize="2"
                    :extensions="extensions"
                />
            </div>
        </div>
        <ShowTable
        :query_arr="query_arr"
        :query_content="query_content"
        :query_content_title="query_content_title"
        :selected_id="selected_id"
        @select="select"
        @getdetail="getdetail"
        />
    </div>
</template>

<script>
import { useStore } from 'vuex';
import { ref } from "vue";
import { Codemirror } from "vue-codemirror";
import { sql } from "@codemirror/lang-sql";
import ToolBar from '../components/ToolBar.vue';
import ShowTable from '../components/ShowTable.vue';
import $ from 'jquery';

export default {
    name: "HomeView",
    components: {
        Codemirror,
        ToolBar,
        ShowTable,
    },
    setup() {
        const store = useStore();
        let localstorage = window.localStorage;

        let query_arr = ref([]);
        let query_content = ref([]);
        let query_content_title = ref(['用户名','查询时间']);
        let selected_id = ref(0);

        store.dispatch('checkout',{
            success(){
                const access = localstorage.getItem('access');
                $.ajax({
                    url: "https://app3148.acapp.acwing.com.cn/getquery/",
                    type: "POST",
                    data: {
                        query_id: 0,
                    },
                    headers: {
                        'Authorization': "Bearer " + access,
                    },
                    success(res){
                        query_content.value = res.query_content;
                        query_content_title.value = res.query_content_title;
                    }
                })
            },
            error(){
                
            }
        });

        const code = ref(`select col1,col2,col3 from HIGGS       /*登录后运行 样例 表名HIGGS 列名col1-col29*/`);
        const extensions = [sql()];
        let request;
        let isrunning = ref(false);

        const submitcode = () => {
            let access = '';
            isrunning.value = true;
            store.dispatch('checkout',{
                success(){
                    access = localstorage.getItem('access');
                    request = $.ajax({
                        url: 'https://app3148.acapp.acwing.com.cn/query/',
                        type: 'POST',
                        data: {
                            'code': code.value,
                        },
                        headers: {
                            'Authorization': "Bearer " + access,
                        },
                        success(res) {
                            isrunning.value = false;
                            request = null;
                            selected_id.value = res.query_id;
                            query_arr.value.push(parseInt(res.query_id));
                            query_content.value = res.query_content;
                            query_content_title.value = res.query_content_title;
                        }
                    })
                },
                error(){
                    
                }
            });
            
        };

        const stopcode = () => {
            if(isrunning.value){
                store.dispatch('checkout',{
                    success(){
                        request.abort();
                        request = null;
                    },
                    error(){
                        
                    }
                });
            }
        }

        const copycode = () => {
            store.dispatch('checkout',{
                success(){
                    const el = document.createElement('textarea');
                    el.value = code.value; 
                    el.setAttribute('readonly', '');
                    el.style.position = 'absolute';
                    el.style.left = '-9999px';
                    document.body.appendChild(el);
                    const selected = document.getSelection().rangeCount > 0 ? document.getSelection().getRangeAt(0) : false;
                    el.select();
                    document.execCommand('copy');
                    document.body.removeChild(el);
                    if (selected) {
                        document.getSelection().removeAllRanges();
                        document.getSelection().addRange(selected);
                    }
                },
                error(){
                    
                }
            });
            
        };

        const pastecode = () => {
            console.log("pastecode");
        };

        const deletecode = () => {
            store.dispatch('checkout',{
                success(){
                    code.value = '';
                },
                error(){
                    
                }
            });
        };

        const select = query_id => {
            const access = localstorage.getItem('access');
            $.ajax({
                url: "https://app3148.acapp.acwing.com.cn/getquery/",
                type: "POST",
                data: {
                    query_id,
                },
                headers: {
                    'Authorization': "Bearer " + access,
                },
                success(res){
                    selected_id.value = query_id;
                    query_content.value = res.query_content;
                    query_content_title.value = res.query_content_title;
                }
            })
        }

        const getdetail = query_id => {
            const access = localstorage.getItem('access');
            let isfind = false;
            for(let item of query_arr.value){
                if (parseInt(item) === parseInt(query_id)) {
                    selected_id.value = query_id;
                    isfind = true;
                    break;
                }
            }
            if (!isfind) {
                query_arr.value.push(parseInt(query_id));
                $.ajax({
                    url: "https://app3148.acapp.acwing.com.cn/getquery/",
                    type: "POST",
                    data: {
                        query_id,
                    },
                    headers: {
                        'Authorization': "Bearer " + access,
                    },
                    success(res){
                        selected_id.value = query_id;
                        query_content.value = res.query_content;
                        query_content_title.value = res.query_content_title;
                    }
                })
            }
        }

        return {
            query_arr,
            query_content,
            query_content_title,
            selected_id,
            select,
            getdetail,
            code,
            extensions,
            submitcode,
            stopcode,
            copycode,
            pastecode,
            deletecode,
        };
    }
}
</script>

<style scoped>
.container{
    margin-top: 3vh;
}
.card-body{
    padding: 0;
}
</style>